import { Component, OnInit, Input } from '@angular/core';
import { Mobile } from '../model/mobile.model';
import { RestService } from '../rest.service';

@Component({
  selector: 'app-product-overview',
  templateUrl: './product-overview.component.html',
  styleUrls: ['./product-overview.component.css']
})
export class ProductOverviewComponent implements OnInit {

@Input() mobile: Mobile;
private cartMessage: string;

  constructor(private service: RestService) {
    this.mobile = new Mobile();
   }

  ngOnInit() {
  }

  addToCart(mobileId: string) {
    this.service.addToCartList(mobileId).subscribe((res) => {

      if (res == true) {
        this.cartMessage = '*Added to Cart List'
      }
      else {
          this.cartMessage = '* Already in Cart List';
      }

    },
      (err) => {
        console.log("Error in add to Cart List");
      });
  }
  sendProductId(mobileId: Mobile) {
    this.service.receiveMobileId(mobileId);
    
  }

}
