import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpErrorResponse } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { map, catchError, tap } from 'rxjs/operators';
import { CustomerModel } from './model/customer.model';
import { Router } from '@angular/router';
import { Mobile } from './model/mobile.model';

@Injectable({
  providedIn: 'root'
})
export class RestService {

  private mobileId: Mobile;
  private customerId: string;

  constructor(private http: HttpClient, private route: Router) {
  }


  httpOptions = {
    headers: new HttpHeaders({
      'Content-Type': 'application/json',
      'Cache-Control': 'no-catch',
      'Access-Control-Allow-Methods': 'GET',
      'Access-Control-Request-Methods': 'POST',
      'Access-Control-Allow-Origin': '*'
    })
  };
  private extractData(res: Response) {
    let body = res;
    console.log(body);
    return body || {};
  }
  isCustomerAvailable(customer: CustomerModel): Observable<any> {
    this.customerId = customer.customerId;
    const endpoint = 'http://localhost:8001/online-store/customers/' + customer.customerId;
    return this.http.get(endpoint).pipe(map(this.extractData));

  }
  registerNewCustomer(customer: CustomerModel): Observable<any> {
    const endpoint = 'http://localhost:8001/online-store/register';
    return this.http.post<CustomerModel>(endpoint, JSON.stringify({ customer }), this.httpOptions);
  }
  getProduct(): Observable<any> {
    const endpoint = 'http://localhost:8001/online-store/products';
    return this.http.get(endpoint).pipe(map(this.extractData));
  }

  receiveMobileId(mobileId: Mobile) {
    this.mobileId = mobileId;
  }

  getProductDetails(): Observable<any> {
    const endpoint = 'http://localhost:8001/online-store/show-details/' + this.mobileId;
    return this.http.get(endpoint).pipe(map(this.extractData));
  }
  getCartDetails(): Observable<any> {
    const endpoint = 'http://localhost:8001/online-store/cart-details/' + this.customerId;
    return this.http.get(endpoint).pipe(map(this.extractData));
  }
  
  getSearchMobile(searchMobile: string): Observable<any> {
    const endpoint = 'http://localhost:8001/online-store/search-mobile/' + searchMobile;
    return this.http.get(endpoint).pipe(map(this.extractData));
  }
  addToCartList(mobileId: string): Observable<any> {
    const endpoint = 'http://localhost:8001/online-store/add-cart/' + this.customerId + '/' + mobileId;
    return this.http.get(endpoint).pipe(map(this.extractData));
  }
  
  deleteCartProduct(mobileId: string): Observable<any> {
    const endpoint = 'http://localhost:8001/online-store/delete-cart/' + this.customerId+'/'+mobileId;
    return this.http.delete(endpoint).pipe(map(this.extractData));
  }
}


