import { Component, OnInit, Injectable, Input } from '@angular/core';
import { RestService } from '../rest.service';
import { Mobile} from '../model/mobile.model';

@Component({
  selector: 'app-show-detail-page',
  templateUrl: './show-detail-page.component.html',
  styleUrls: ['./show-detail-page.component.css'],
})

export class ShowDetailPageComponent implements OnInit {

  private mobile: Mobile;
  private cartMessage: string;

  constructor(private service: RestService) {
    this.mobile = new Mobile();
    
   }

  ngOnInit() {
    this.service.getProductDetails().subscribe((res)=>{
      console.log("subscribe response show details page");
      this.mobile = JSON.parse(JSON.stringify(res));
      console.log(this.mobile);
    },
    (err)=>{
      console.log("subscribe error show details page");
    });
  }

  

addToCart(mobileId: string){
  this.service.addToCartList(mobileId).subscribe((res)=>{
    if (res == true) {
      this.cartMessage = '*Added to Cart List'
    }
    else {
        this.cartMessage = '* Already in Cart List';
    }
  },
  (err)=>{
    console.log("Show Details Page Add cart error")
  });
}

}
