import { Component, OnInit } from '@angular/core';
import { RestService } from '../rest.service';
import { Mobile } from '../model/mobile.model';
import { isNullOrUndefined } from 'util';
import { isEmpty } from 'rxjs/operators';

@Component({
  selector: 'app-cart-page',
  templateUrl: './cart-page.component.html',
  styleUrls: ['./cart-page.component.css']
})
export class CartPageComponent implements OnInit {

  private mobiles: Mobile[];
  private mobile: Mobile;
  private informMessage: string = '';

  constructor(private service: RestService) { }

  ngOnInit() {
    this.service.getCartDetails().subscribe((res)=>{
      console.log("CartDetailPage Response");
      this.mobiles = JSON.parse(JSON.stringify(res));
      console.log(this.mobiles);
      if(this.mobiles.length===0){
        this.informMessage = 'None Products in Your Cart List';
      }
    },
    (err)=>{
      this.informMessage = "Sorry, Somthing went wrong Please try again Later";
    });
  }
  sendProductId(mobileId: Mobile){
    this.service.receiveMobileId(mobileId);
    this.service.getProductDetails().subscribe((res)=>{
      this.mobile = JSON.parse(JSON.stringify(res));
    },
    (err)=>{
      console.log("Error Respnse in send Product Id");
    });
  }
  deleteCartProduct(mobileId: string){
    this.service.deleteCartProduct(mobileId).subscribe((res)=>{

    },
    (err)=>{
      console.log("Error response in delete cart Page details");
    });
    this.ngOnInit();
  }
}

