import { Component, OnInit, ViewChild, ContentChild } from '@angular/core';
import { CustomerModel } from '../model/customer.model';
import { RestService } from '../rest.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-login-page',
  templateUrl: './login-page.component.html',
  styleUrls: ['./login-page.component.css']
})

export class LoginPageComponent implements OnInit {

  private customer: CustomerModel;
  private validationError: string;
  private showPassword: string= 'password';


  constructor(private service: RestService, private route: Router) {
    this.customer = new CustomerModel();
  }

  ngOnInit() {

  }

  authenticateCustomer() {

    if (this.customer.customerId != '' && this.customer.password != '') {

      this.service.isCustomerAvailable(this.customer).subscribe((res) => {
        var matchedCustomer: CustomerModel;
        matchedCustomer = res;
        if (matchedCustomer.password == this.customer.password) {
          this.route.navigateByUrl('product');
        } else {
          this.validationError = "Invalid Username or Password";
        }

      }, (err) => {
        console.log(err + " login page error");
      });
    } else {
      this.validationError = "Fill CustomerId and Password then Login"
    }
  }

showTextPassword(showPassword: string){
  if(showPassword== 'password'){
    this.showPassword = 'text';
  }else{
    this.showPassword = 'password';
  }
}
}



