import { Component, OnInit } from '@angular/core';
import { RestService } from '../rest.service';
import { CustomerModel } from '../model/customer.model';
import { Router } from '@angular/router';

@Component({
  selector: 'app-register-page',
  templateUrl: './register-page.component.html',
  styleUrls: ['./register-page.component.css']
})
export class RegisterPageComponent implements OnInit {

  private customer: CustomerModel;
  private instructionMessage: string;
  private showPassword: string = 'password';
    constructor(private service: RestService, private router: Router) {
    this.customer = new CustomerModel();
  }

  ngOnInit() {
  }

  registerCustomer() {
    if (this.customer.firstName != '' && this.customer.lastName != '' && this.customer.mobileNumber != '' &&
      this.customer.customerId != '' && this.customer.password != '') {
      this.service.registerNewCustomer(this.customer).subscribe((res) => {
        this.instructionMessage = "Successfully Registered Customer Goto Login Page";
      }, (err) => {
        this.instructionMessage = "You are already a customer in Tumkart";
      });
    } else {
      this.instructionMessage = "First, Fill all details then Register";
    }
  }
  showTextPassword(showPassword: string){
    if(showPassword== 'password'){
      this.showPassword = 'text';
    }else{
      this.showPassword = 'password';
    }
  }
}
